"use client";

import React, { useState, useEffect, useMemo } from "react";

/* ------------------------------- SORT ICON ------------------------------- */
const SortArrow = ({ active, direction }) => {
  return (
    <div className="flex flex-col items-center justify-center ml-1 mt-[2px] leading-[0.4]">
      <div
        className={`w-2 h-2 rotate-45 border-t-2 border-l-2 ${
          active && direction === "asc" ? "border-gray-700" : "border-gray-400"
        }`}
      ></div>

      <div
        className={`w-2 h-2 rotate-45 border-b-2 border-r-2 mt-[3px] ${
          active && direction === "desc" ? "border-gray-700" : "border-gray-400"
        }`}
      ></div>
    </div>
  );
};

export default function ReportDashboard() {
  const API_URL =
    "https://matrimonial-backend-7ahc.onrender.com/api/report/list";

  const [reports, setReports] = useState([]);
  const [totalReports, setTotalReports] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);

  const LIMIT = 5;

  /* ------------------------------- API CALL ------------------------------- */
  const fetchReports = async () => {
    try {
      const res = await fetch(`${API_URL}?page=${currentPage}&limit=${LIMIT}`);
      const data = await res.json();

      if (data.success) {
        setReports(data.data);
        setTotalReports(data.totalReports);
      }
    } catch (err) {
      console.log("Error fetching reports:", err);
    }
  };

  useEffect(() => {
    fetchReports();
  }, [currentPage]);

  const totalPages = Math.ceil(totalReports / LIMIT);

  /* ------------------------------- SORTING ------------------------------- */
  const [sortConfig, setSortConfig] = useState({ key: null, direction: "asc" });

  const sortBy = (key) => {
    setSortConfig((prev) => ({
      key,
      direction:
        prev.key === key && prev.direction === "asc" ? "desc" : "asc",
    }));
  };

  const sortedData = useMemo(() => {
    if (!sortConfig.key) return reports;

    const getName = (u) =>
      `${u?.firstName || ""} ${u?.lastName || ""}`.trim().toLowerCase();

    return [...reports].sort((a, b) => {
      let x = "",
        y = "";

      if (sortConfig.key === "reportedUser") {
        x = getName(a.reportedUser);
        y = getName(b.reportedUser);
      }

      if (sortConfig.key === "reporterUser") {
        x = getName(a.reporter);
        y = getName(b.reporter);
      }

      if (sortConfig.key === "reportDate") {
        x = new Date(a.createdAt);
        y = new Date(b.createdAt);
      }

      if (x < y) return sortConfig.direction === "asc" ? -1 : 1;
      if (x > y) return sortConfig.direction === "asc" ? 1 : -1;

      return 0;
    });
  }, [reports, sortConfig]);

  /* ------------------------------- SAFE GETTERS ------------------------------- */
  const getPhoto = (u) => u?.profileImage || "/default-profile.png";
  const getName = (u) =>
    `${u?.firstName || ""} ${u?.lastName || ""}`.trim() || "Unknown";
  const getId = (u) => (u?._id ? `#${u._id}` : "#----");
  const getGender = (u) => u?.gender || "N/A";

  const getStatusDot = (s) => {
    s = s?.toLowerCase();
    return s === "approved"
      ? "bg-green-500"
      : s === "pending"
      ? "bg-yellow-500"
      : "bg-red-500";
  };

  return (
    <div className="bg-white p-2">
      <h2 className="text-xl font-bold mb-3">Reported Content</h2>

      <div className="w-full h-[1px] bg-gray-500 mb-2"></div>

      <table className="w-full text-sm text-left">
        <thead>
          <tr className="border-b border-gray-500">
            <th
              className="py-3 px-2 cursor-pointer"
              onClick={() => sortBy("reportedUser")}
            >
              <div className="flex items-center gap-1">
                Reported User
                <SortArrow
                  active={sortConfig.key === "reportedUser"}
                  direction={sortConfig.direction}
                />
              </div>
            </th>

            <th
              className="py-3 px-2 cursor-pointer"
              onClick={() => sortBy("reportDate")}
            >
              <div className="flex items-center gap-1">
                Report Date
                <SortArrow
                  active={sortConfig.key === "reportDate"}
                  direction={sortConfig.direction}
                />
              </div>
            </th>

            <th
              className="py-3 px-2 cursor-pointer"
              onClick={() => sortBy("reporterUser")}
            >
              <div className="flex items-center gap-1">
                Reporter User
                <SortArrow
                  active={sortConfig.key === "reporterUser"}
                  direction={sortConfig.direction}
                />
              </div>
            </th>

            <th className="py-3 px-2">Reason</th>
            <th className="py-3 px-2">Status</th>
          </tr>
        </thead>

        <tbody>
          {sortedData.map((item) => (
            <tr key={item._id} className="border-t border-gray-300">
              <td className="py-3 px-2">
                <div className="flex items-center gap-3">
                  <img
                    src={getPhoto(item.reportedUser)}
                    className="w-11 h-11 rounded-full object-cover"
                  />
                  <div>
                    <div className="font-semibold">
                      {getName(item.reportedUser)}
                    </div>
                    <div className="text-xs text-gray-600">
                      {getId(item.reportedUser)} /{" "}
                      {getGender(item.reportedUser)}
                    </div>
                  </div>
                </div>
              </td>

              <td className="py-3 px-2">
                {new Date(item.createdAt).toLocaleDateString("en-GB", {
                  day: "2-digit",
                  month: "long",
                  year: "numeric",
                })}
              </td>

              <td className="py-3 px-2">
                <div className="flex items-center gap-3">
                  <img
                    src={getPhoto(item.reporter)}
                    className="w-11 h-11 rounded-full object-cover"
                  />
                  <div>
                    <div className="font-semibold">{getName(item.reporter)}</div>
                    <div className="text-xs text-gray-600">
                      {getId(item.reporter)} / {getGender(item.reporter)}
                    </div>
                  </div>
                </div>
              </td>

              <td className="py-3 px-2">{item.title}</td>

              <td className="py-3 px-2">
                <div className="flex items-center gap-2">
                  <span
                    className={`w-3 h-3 rounded-full ${getStatusDot(
                      item.status
                    )}`}
                  ></span>
                  {item.status}
                </div>
              </td>
            </tr>
          ))}
        </tbody>
      </table>

      {/* ------------------------------- PAGINATION ------------------------------- */}
      <div className="flex justify-center items-center gap-3 mt-4 text-sm">
        <button
          disabled={currentPage === 1}
          onClick={() => setCurrentPage((p) => p - 1)}
        >
          ‚óÄ Prev
        </button>

        {[...Array(totalPages)].map((_, i) => (
          <button
            key={i}
            onClick={() => setCurrentPage(i + 1)}
            className={`${
              currentPage === i + 1
                ? "font-bold underline"
                : "text-gray-600"
            }`}
          >
            {i + 1}
          </button>
        ))}

        <button
          disabled={currentPage === totalPages}
          onClick={() => setCurrentPage((p) => p + 1)}
        >
          Next ‚ñ∂
        </button>
      </div>
    </div>
  );
}
